import React from 'react'
import { Input, Icon, Menu, Button, Dropdown } from 'antd'
import classNames from 'classnames'
import { notification } from 'antd'
import axios from 'axios'

import { parseTimeString, presentTime } from '../../../../utils/ytutil'
import { API_KEY, API_URL, plConfig } from '../../../../utils/config'

const Search = Input.Search

class MagazineView extends React.Component {
  state = {
    statePlayingVideoId: null,
    filterkey: null,
    username: window.localStorage.getItem('app.username'),
  }

  allowToAddNewVideo() {
    const { playlistSettings, videos } = this.props

    console.log('playlist settings:', playlistSettings)
    const filteredVideos = videos.filter(v => {
      return v.requester === this.state.username
    })

    if (filteredVideos.length >= playlistSettings.maxSongsPerUser) {
      return false
    }
    return true
  }

  onAdd(videoId) {
    // check video existence
    const filteredVideos = this.props.videos.filter(v => {
      return v.id === videoId
    })
    if (filteredVideos.length > 0) {
      notification.open({
        type: 'warning',
        message: 'This video has already added into the playlist!',
      })
      return
    }

    const isAllowed = this.allowToAddNewVideo()
    if (isAllowed === false) {
      notification.open({
        type: 'warning',
        message: 'You are out of quotation for adding new video, please remove existing videos!',
      })
      return
    }

    axios
      .get(
        `https://www.googleapis.com/youtube/v3/videos?id=${videoId}&part=contentDetails&key=${API_KEY}`,
      )
      .then(result => {
        if (result.data.items && result.data.items.length > 0) {
          const videoDetails = result.data.items[0]
          // get duration
          const duration = parseTimeString(videoDetails.contentDetails.duration)
          if (duration > plConfig.maxVideoLen) {
            notification.open({
              type: 'warning',
              message: "Video's length is exceded 5 mins, please select another shorter video!",
            })
          } else {
            axios
              .get(
                `${API_URL}/videos/add?videoId=${videoId}&userId=${
                  this.state.username
                }&playlistId=${this.props.playplistId}`,
              )
              .then(res => {
                if (res.data && res.data.id) {
                  this.props.addVideo(res.data)

                  notification.open({
                    type: 'success',
                    message: 'Video is added in the playlist!',
                  })
                }
              })
          }

          console.log('duration:', duration)
        } else {
          notification.open({
            type: 'warning',
            message: "Video's Id is invalid from youtube, please check again!",
          })
        }
      })
  }

  removeVideo(videoId) {
    axios
      .get(`${API_URL}/videos/remove?videoId=${videoId}&playlistId=${this.props.playplistId}`)
      .then(res => {
        if (res.data) {
          this.props.removeVideo(videoId)

          notification.open({
            type: 'success',
            message: 'Video is removed from the playlist!',
          })
        }
      })
  }

  playVideo(videoId) {
    this.props.play(videoId)
  }

  filterVideo(e) {
    this.setState({ filterkey: e.key })
  }

  actions = () => (
    <Menu onClick={this.filterVideo.bind(this)}>
      <Menu.Item key="all">All</Menu.Item>
      <Menu.Item key="my-videos">My videos</Menu.Item>
    </Menu>
  )

  componentWillReceiveProps(nextProps) {
    const { playingVideoId } = nextProps
    this.setState({ statePlayingVideoId: playingVideoId })
  }

  render() {
    let { playingVideoId, videos } = this.props
    let { statePlayingVideoId, filterkey, username } = this.state

    if (statePlayingVideoId === null) {
      statePlayingVideoId = playingVideoId
    }

    if (filterkey === 'my-videos') {
      videos = videos.filter(v => {
        return v.requester === username
      })
    }

    return (
      <div className="video-page__sidebar card">
        <div className="video-page__sidebar-head">
          <Dropdown overlay={this.actions()}>
            <a className="ant-dropdown-link" href="javascript: void(0);">
              Filter videos <Icon type="down" />
            </a>
          </Dropdown>
          <div className="video-page__add pull-right">
            <Search
              placeholder="Youtube video's id"
              enterButton="Add"
              onSearch={value => this.onAdd(value)}
            />
          </div>
        </div>
        <ul className="video-page__watch-next">
          {videos.map((video, index) => (
            <li className="video-page__next-item clearfix" key={index}>
              <a href="javascript: void(0);" className="video-page__next-item-link">
                <div className="video-page__item-thumb">
                  <img
                    className="video-page__item-thumb-img"
                    src={video.cover.url}
                    alt={video.name}
                    onClick={() => this.playVideo(video.id)}
                  />
                </div>

                <div className="video-page__item-time">
                  <span
                    className={classNames(
                      'video-page__item-name',
                      playingVideoId === video.id ? 'video-page__active' : '',
                    )}
                    onClick={() => this.playVideo(video.id)}
                  >
                    {presentTime(video.duration)}
                  </span>
                  {playingVideoId === video.id && (
                    <Icon
                      type="sound"
                      style={{
                        fontSize: '22px',
                        color: '#0088ff',
                        display: 'block',
                      }}
                    />
                  )}
                  {playingVideoId !== video.id && video.requester === username && (
                    <Button
                      icon="close"
                      style={{ marginTop: '10px' }}
                      onClick={() => this.removeVideo(video.id)}
                    />
                  )}
                </div>

                <div className="video-page__item-descr">
                  <span
                    className={classNames(
                      'video-page__item-name',
                      playingVideoId === video.id ? 'video-page__active' : '',
                    )}
                  >
                    {video.name}
                  </span>
                  <span className="video-page__item-author">{video.author}</span>
                  <span className="video-page__item-author">Added by {video.requester}</span>
                </div>
              </a>
            </li>
          ))}
        </ul>
      </div>
    )
  }
}

export default MagazineView



