import React from 'react'
import MagazineView from './MagazineView'
import PlaylistView from './PlaylistView'
import PlayerView from './PlayerView'
import './style.scss'
import axios from 'axios'
import { API_URL } from '../../../../utils/config'
import { shuffle } from '../../../../utils/ytutil'

class YouTubeView extends React.Component {
  state = {
    playplistId: '2053363501372723',
    playingVideoIndex: null,
    playingVideoId: null,
    playlistSettings: {},
    videos: [],
  }

  play = videoId => {
    let videoIndex = 0
    const { videos } = this.state
    for (let i = 0; i <= videos.length; i++) {
      if (videos[i] && videos[i].id === videoId) {
        videoIndex = i
      }
    }
    this.setState({ playingVideoId: videoId, playingVideoIndex: videoIndex })
  }

  changeVideo = (videoId, videoIndex) => {
    console.log(`vidoe change: videoId=${videoId} index: ${videoIndex}`)
    this.setState({ playingVideoId: videoId, playingVideoIndex: videoIndex })
  }

  addVideo = video => {
    let currentList = this.state.videos
    currentList.push(video)
    this.setState({ videos: currentList })
  }

  removeVideo = videoId => {
    let currentList = this.state.videos.filter(video => {
      return video.id !== videoId
    })
    this.setState({ videos: currentList })
  }

  getVideos() {
    axios.get(`${API_URL}/videos/list?playlistId=${this.state.playplistId}`).then(result => {
      let tempVideos = []
      let tempVideoIds = []
      Object.entries(result.data).forEach(entry => {
        tempVideos.push(entry[1])
        tempVideoIds.push(entry[1].id)
      })

      const shuffVideos = shuffle(tempVideos)
      this.setState({
        videos: shuffVideos,
        playingVideoIndex: 0,
        playingVideoId: shuffVideos[0].id,
      })
    })
  }

  getPlayplist() {
    axios.get(`${API_URL}/playlists/view?playlistId=${this.state.playplistId}`).then(result => {
      console.log('daf', result.data.settings)
      this.setState({ playlistSettings: result.data.settings })
    })
  }

  componentWillMount() {
    this.getPlayplist()
    this.getVideos()
  }

  render() {
    const { playlistSettings, playingVideoId, playingVideoIndex, playplistId, videos } = this.state

    return (
      <section className="card">
        <div className="card-header">
          <div className="utils__title">
            <strong>Grand team - playlist</strong>
          </div>
        </div>
        <div className="card-body">
          <div className="video-page clearfix">
            {playingVideoIndex !== null && (
              <PlayerView
                playingVideoIndex={playingVideoIndex}
                videos={videos}
                changeVideo={this.changeVideo}
              />
            )}
            {playingVideoIndex !== null && (
              <MagazineView
                playplistId={playplistId}
                play={this.play}
                playingVideoId={playingVideoId}
                playlistSettings={playlistSettings}
                videos={videos}
                addVideo={this.addVideo}
                removeVideo={this.removeVideo}
              />
            )}
          </div>
        </div>
      </section>
    )
  }
}

export default YouTubeView



