import React from 'react'
import ReactPlayer from 'react-player'
import { Icon } from 'antd'
import './style.scss'

class PlayerView extends React.Component {
  state = {
    played: 0,
    playing: true,
    playingIndex: 0,
  }

  onPlay = () => {
    console.log('onPlay')
    this.setState({ playing: true })
  }

  onSeekChange = e => {
    console.log('onSeekChange')
    this.setState({ played: parseFloat(e.target.value) })
  }

  playPause = () => {
    this.setState({ playing: !this.state.playing })
  }

  onPause = () => {
    console.log('onPause')
    this.setState({ playing: false })
  }

  onProgress = state => {
    // console.log("onProgress", state);
    // We only want to update time slider if we are not currently seeking
    if (!this.state.seeking) {
      this.setState(state)
    }
  }

  onEnded = () => {
    console.log('onEnded')
    this.playNext()
  }

  playNext() {
    const { videos } = this.props
    let nextIndex = this.state.playingIndex + 1
    if (nextIndex >= videos.length) {
      nextIndex = 0
    }
    this.setState({ playingIndex: nextIndex })
    this.props.changeVideo(videos[nextIndex].id, nextIndex)
  }

  playPrevious() {
    const { videos } = this.props
    let nextIndex = this.state.playingIndex - 1
    if (nextIndex < 0) {
      nextIndex = videos.length - 1
    }
    this.setState({ playingIndex: nextIndex })
    this.props.changeVideo(videos[nextIndex].id, nextIndex)
  }

  componentWillReceiveProps(nextProps) {
    const { playingVideoIndex } = nextProps
    //console.log("move to selected song", playingVideoIndex);
    if (playingVideoIndex !== this.state.playingIndex) {
      this.setState({ playingIndex: playingVideoIndex })
    }
  }

  render() {
    const { played, playing, playingIndex } = this.state
    const { videos } = this.props
    const video = videos[playingIndex]

    return (
      <div className="video-page__main-content">
        <div>
          <ReactPlayer
            url={`https://www.youtube.com/watch?v=${video.id}`}
            width="540px"
            height="360px"
            playing={playing}
            onPlay={this.onPlay}
            onProgress={this.onProgress}
            onPause={this.onPause}
            onEnded={this.onEnded}
          />
        </div>
        <div className="video-page__descr">
          <strong>
            {video.name} - {video.author}
          </strong>
        </div>
        <div className="video-page__player-control-container">
          <div style={{ marginTop: '10px' }}>
            <i
              className="icmn-shuffle"
              style={{
                fontSize: '20px',
                color: '#686e71',
                marginTop: '13px',
                float: 'left',
                paddingLeft: '20px',
              }}
            />
            <i
              className="icmn-previous2"
              onClick={() => this.playPrevious()}
              style={{ fontSize: '30px', color: '#686e71', marginRight: '25px', cursor: 'pointer' }}
            />
            {playing && (
              <i
                className="icmn-pause"
                style={{
                  fontSize: '40px',
                  color: '#686e71',
                  marginRight: '25px',
                  cursor: 'pointer',
                }}
                onClick={() => this.playPause()}
              />
            )}
            {playing === false && (
              <i
                className="icmn-play2"
                style={{
                  fontSize: '40px',
                  color: '#686e71',
                  marginRight: '25px',
                  cursor: 'pointer',
                }}
                onClick={() => this.playPause()}
              />
            )}

            <i
              className="icmn-next2"
              onClick={() => this.playNext()}
              style={{
                fontSize: '30px',
                color: '#686e71',
                marginTop: '10px',
                marginRight: '25px',
                cursor: 'pointer',
              }}
            />
            <i
              className="icmn-volume-medium"
              style={{
                fontSize: '25px',
                color: '#686e71',
                marginTop: '13px',
                marginRight: '25px',
                float: 'right',
              }}
            />
          </div>

          <input
            className="video-page__player-control-playded"
            type="range"
            min={0}
            max={1}
            step="any"
            value={played}
            onChange={this.onSeekChange}
          />
        </div>
      </div>
    )
  }
}

export default PlayerView



